import { Radar } from 'vue-chartjs';
import { radarChartOptions } from "./config";
export default {
  extends: Radar,
  props: ['data'],
  data: function data() {
    return {
      options: radarChartOptions
    };
  },
  mounted: function mounted() {
    this.renderChart(this.data, this.options);
  }
};