import { Scatter } from 'vue-chartjs';
import { scatterChartOptions } from "./config";
export default {
  extends: Scatter,
  props: ['data'],
  data: function data() {
    return {
      options: scatterChartOptions
    };
  },
  mounted: function mounted() {
    this.renderChart(this.data, this.options);
  }
};